import React from 'react'
import i1 from '../assets/img/i1.jpeg'
import i2 from '../assets/img/i2.jpeg'
import i3 from '../assets/img/i3.jpeg'
import i5 from '../assets/img/i5.jpeg'
import i6 from '../assets/img/i6.jpeg'
import i7 from '../assets/img/i7.jpeg'
import i8 from '../assets/img/i8.jpeg'
import i9 from '../assets/img/i9.jpeg'
import a1 from '../assets/img/a1.jpeg'
import a2 from '../assets/img/a2.jpeg'

const About= ()=>{

    return(
        <section class="page-section" id="about" style={{background:"#17252A"}}>
        <div class="container">
            <div class="text-center">
                <h2 class="section-heading text-uppercase " style={{color:"white"}}>About</h2>
                <h3 class="section-subheading text-muted" style={{color:"white"}}>Believing In Perfection.....</h3>
            </div>
            <ul class="timeline">
                <li>
                    <div class="timeline-image"><img class="rounded-circle img-fluid " src={i1} alt=""  /></div>
                    <div class="timeline-panel">
                        <div class="timeline-heading">
                            <h4 style={{color:"white"}}>March 2018 </h4>
                            <h4 class="subheading">We started from Academy Matches.</h4>
                        </div>
                        <div class="timeline-body"><p class="text-muted">We began our journey with cricket matches for academy. where we received overwhelming response from young cricketers.Which helped the player understand their potential and ability as they got chance to compete with diversity of player and enhance their game.The players kept on playing with us till now</p></div>
                    </div>
                </li>
                <li class="timeline-inverted">
                    <div class="timeline-image"><img class="rounded-circle img-fluid " src={i6} alt=""  /></div>
                    <div class="timeline-panel">
                        <div class="timeline-heading">
                            <h4 style={{color:"white"}}>July 2018</h4>
                            <h4 class="subheading">Landing Of Our First Corporate Tournament.</h4>
                        </div>
                        <div class="timeline-body"><p class="text-muted">So far after organising few academy matches we moved forward to organise our first corporate Cricket tournament. This tournament comprised of corporate Cricket teams of Delhi NCR. Teams and players enjoyed our quality of services throughout this tournament and we received plenty of positive feedback and queries to play in our upcoming tournament.This response of the players encouraged and motivated us to bring up few more tournaments.</p></div>
                    </div>
                </li>
                <li>
                    <div class="timeline-image"><img class="rounded-circle img-fluid " src={i7} alt=""  /></div>
                    <div class="timeline-panel">
                        <div class="timeline-heading">
                            <h4 style={{color:"white"}}>October 2018</h4>
                            <h4 class="subheading">Launching Of Pad Up League.</h4>
                        </div>
                        <div class="timeline-body"><p class="text-muted">This  was one of our major tournaments full of new concept and ideas for budding cricketers to make them play with other talented players and for players it was hard to make it in this tournament as it has selection round at the beginning stage so we ensured the players qualifying those stage were in the tournament.</p></div>
                    </div>
                </li>
                <li class="timeline-inverted">
                    <div class="timeline-image"><img class="rounded-circle img-fluid " src={i1} alt="" /></div>
                    <div class="timeline-panel">
                        <div class="timeline-heading">
                            <h4 style={{color:"white"}}>March 2019</h4>
                            <h4 class="subheading">Phase Two Expansion</h4>
                        </div>
                        <div class="timeline-body"><p class="text-muted">On this time we were about to expand our services in matter of region and we did it successfully from that period of we offer our services in Gurgaon and Noida. Which made players of that region enjoy our services too and now we organise events in both regions</p></div>
                    </div>
                </li>

                <li>
                    <div class="timeline-image"><img class="rounded-circle img-fluid " src={i9} alt="" /></div>
                    <div class="timeline-panel">
                        <div class="timeline-heading">
                            <h4 style={{color:"white"}}>May 2019 </h4>
                            <h4 class="subheading">Completed Two Journies...</h4>
                        </div>
                        <div class="timeline-body"><p class="text-muted">Here we achieved the successful completion of two tournament.Which helped our team to move for more.</p></div>
                    </div>
                </li>

                <li class="timeline-inverted">
                    <div class="timeline-image"><img class="rounded-circle img-fluid " src={a1} alt="" /></div>
                    <div class="timeline-panel">
                        <div class="timeline-heading">
                            <h4 style={{color:"white"}}>August 2019</h4>
                            <h4 class="subheading">Expansion in Gurugram,Delhi Noida All Togehter.. </h4>
                        </div>
                        <div class="timeline-body"><p class="text-muted">Till date we were available for seperate regions at a time but from now we were available for organising all over Delhi NCR, Gurgaon, Noida and at same point of time we were offering our services in all the regions even matches of same tournament were played on all these places.</p></div>
                    </div>
                </li>

                <li>
                    <div class="timeline-image"><img class="rounded-circle img-fluid " src={a2} alt="" /></div>
                    <div class="timeline-panel">
                        <div class="timeline-heading">
                            <h4 style={{color:"white"}}>January 2020 </h4>
                            <h4 class="subheading">Started Famous Tournament #Pro30...</h4>
                        </div>
                        <div class="timeline-body"><p class="text-muted">At this time our team was ready to organise the 2.0 version of our successful and first tournament which was praised lot among the player of Delhi NCR and the tournament came up with a name PRO 30 which was 30 over game and was one of the most awaited tournament for the players as well as for us</p></div>
                    </div>
                </li>

                <li class="timeline-inverted">
                    <div class="timeline-image"><img class="rounded-circle img-fluid " src={i5} alt=""  /></div>
                    <div class="timeline-panel">
                        <div class="timeline-heading">
                            <h4 style={{color:"white"}}>March 2020</h4>
                            <h4 class="subheading">Till Now Running 10 Tournaments parallelly </h4>
                        </div>
                        <div class="timeline-body"><p class="text-muted">We never stop with those of major tournaments running till now we have many tournaments running parallel to those and still our team is working 10+ tournament that are going on.</p></div>
                    </div>
                </li>

                <li class="timeline-inverted">
                    <div class="timeline-image">
                        <h4>Be Part<br />Of Our<br />Story!</h4>
                    </div>
                </li>
            </ul>
            <br/>
            <hr style={{background:"white"}}/>
        </div>
    </section>
   
    )

}

export default About