import React from 'react';
import { Nav, Navbar, NavbarToggler, Collapse, NavItem, NavbarBrand   } from 'reactstrap';
import { NavLink, } from 'react-router-dom';
import {Link} from 'react-scroll'
import './navbarToggler.css'
import logo1 from "../assets/img/logo1.jpeg"


class Header extends React.Component{
    constructor(props) {
        super(props);
    
        this.toggleNav = this.toggleNav.bind(this);
        this.toggleModal = this.toggleModal.bind(this);
        
        this.state = {
          isNavOpen: false,
          isToggleOpen: false
        };
      }

      toggleNav() {
        this.setState({
          isNavOpen: !this.state.isNavOpen
        });
      }

      toggleModal(){
          this.setState({
              isToggleOpen: !this.state.isToggleOpen
          })
          
      }
    
    render()
    {
            return (
                <>
                

                <div id="Headers" >
                <Navbar className="navbar navbar-expand-lg ml-auto dark fixed-top" expand="md" id="mainNav" style={{background:"dark"}}>
                            <div className="container">
                            
                            <Link to="Headers" className="nav-link" smooth={true} ><img  src={logo1}  height="30" width="70"/></Link>
                                <NavbarToggler className="navbarToggler navbar-toggler-right ml-auto " onClick={this.toggleNav}  style={{color:"black", backgroundColor:"orange"}}>Menu</NavbarToggler>
                                
                                <Collapse isOpen={this.state.isNavOpen} navbar>
                                    <Nav navbar>
                                    <NavItem>
                                    <Link className="nav-link" to="services" smooth={true}> <NavLink className="nav-link" to='/services'>Services</NavLink></Link>
                                    
                                    </NavItem>
                                    <NavItem>
                                     <Link className="nav-link" to="portfolio" smooth={true}> <NavLink className="nav-link" to='/portfolio'>Portfolio</NavLink></Link>
                                    </NavItem>
                                    <NavItem>
                                    <Link className="nav-link" to="about" smooth={true}><NavLink className="nav-link"  to='/about'>About</NavLink></Link>
                                    </NavItem>
                                    <NavItem>
                                    <Link className="nav-link" to="team" smooth={true}> <NavLink className="nav-link" to='/team'>Team</NavLink></Link>
                                    </NavItem>
                    
                                    <NavItem>
                                    <Link className="nav-link" to="contact" smooth={true}><NavLink className="nav-link" to='/contact'> Contact</NavLink></Link>
                                    </NavItem>
                                    </Nav>
                                    
                                </Collapse>
                            </div>
                        </Navbar>
                </div>
                
                <header class="masthead">
                    <div class="container">
                        <div class="masthead-subheading">Welcome To THE FAMILY GROUP</div>
                        <div ><h5>..</h5></div>
                        <a class="btn btn-primary text-uppercase js-scroll-trigger" href="https://docs.google.com/forms/d/e/1FAIpQLScOQFoAdNe8oYqLDE4ZaSA9GPaZcHqUp6_HJn9Wlmw2VGQ8Vw/viewform?usp=sf_link">REGISTER FOR PAD UP!!</a>
                        <br></br>
                        <br></br>
                        <a class="btn btn-primary text-uppercase js-scroll-trigger" href="https://paytm.me/Mz3-kgI">Pay Now FOR PAD UP!!</a>
                    </div>
                </header>
                </>
                
            )
        }
}

export default Header;
