import React, {useState} from 'react';
import { Row, Col, Form, Button, FormGroup } from 'react-bootstrap'
import Axios from 'axios';




class PadupForm extends React.Component{

    constructor(props)
    {
        super(props);

        this.state ={
            name : "",
            email: "",
            fatherName: "",
            motherName: "",
            mobileNumber: "",
            dob: "",
            role: "",
            style: "",
            referral: "",
            city: ""
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    handleSubmit = (event) =>{
        
        console.log(this.state);

        Axios({
            method: "POST", 
            url:"http://localhost:3002/send", 
            data:  this.state
          }).then((response)=>{
            if (response.data.status === 'success'){
              alert("Message Sent."); 
              this.resetForm()
            }else if(response.data.status === 'fail'){
              alert("Message failed to send.")
              
            }
          })
       
    
    event.preventDefault();
    }

    handleChange = ( event ) =>{

        let name = event.target.name;
        let val = event.target.value;

        this.setState({[name]: val });
    }

    
    render()
    {
       
        return(
                <div>
            <h2 style={{marginTop: "2rem"}}>Pad Up 2020 Registration Form</h2>
            <Form style = {{margin:"2rem"}} onSubmit = {this.handleSubmit}>
                    <Form.Group as={Row} controlId="formHorizontalEmail">
                        <Form.Label column sm={2}>
                        Email Address*
                        </Form.Label>
                        <Col sm={10}>
                        <Form.Control type="email" name='email' placeholder="Email"  onChange={this.handleChange} />
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="formHorizontalName">
                        <Form.Label column sm={2}>
                        Full Name*
                        </Form.Label>
                        <Col sm={10}>
                        <Form.Control type="text" name='name' placeholder="Name"  onChange={this.handleChange} />
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="formHorizontalFatherName">
                        <Form.Label column sm={2}>
                        Father's Name*
                        </Form.Label>
                        <Col sm={10}>
                        <Form.Control type="text" placeholder="Father's Name" name='fatherName'  onChange={this.handleChange} />
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="formHorizontalMotherName">
                        <Form.Label column sm={2}>
                        Mother's Name*
                        </Form.Label>
                        <Col sm={10}>
                        <Form.Control type="text" placeholder="Mother's Name" name='motherName'   onChange={this.handleChange}/>
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="formHorizontalMobileNumber">
                        <Form.Label column sm={2}>
                        Mobile Number*
                        </Form.Label>
                        <Col sm={10}>
                        <Form.Control type="Number" placeholder="Mobile Number" name = 'mobileNumber'  onChange={this.handleChange} />
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="formHorizontalDob">
                        <Form.Label column sm={2}>
                        Date of Birth*
                        </Form.Label>
                        <Col sm={10}>
                        <Form.Control type="text" placeholder="Date Of Birth" name = 'dob'  onChange={this.handleChange} />
                        </Col>
                    </Form.Group>

                    <Form.Group as={Row} controlId="formHorizontalDob">
                        <Form.Label column sm={2}>
                        Style
                        </Form.Label>
                        <Col sm={10}>
                        <Form.Control sm={10} size="sm" as="select" name = 'style'  onChange={this.handleChange} >
                            <option>Left Handed</option>
                            <option>Right Handed</option>
                        </Form.Control>
                    </Col>
                    </Form.Group>
                    
                    <Form.Group as={Row}>
                        <Col sm={{ span: 10, offset: 2 }}>
                        
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="formHorizontalDob">
                        <Form.Label column sm={2}>
                        Role*
                        </Form.Label>
                        <Col sm={10}>
                        <Form.Control sm={10} size="sm" as="select" name = 'role'  onChange={this.handleChange} >
                            <option>Select</option>
                            <option>Opening Batsmen</option>
                            <option>Lower Order Batsmen</option>
                            <option>Middle Order Baatsmen</option>
                            <option>Bowling Allrounder</option>
                            <option>Batting Allrounder</option>
                            <option>Wicket Keeper</option>
                            <option>Off Spinner</option>
                            <option>Leg Spinner</option>
                            <option>Right Arm Pacer</option>
                            <option>Left Arm Pacer</option>
                            <option>Unorthodox</option>
                            <option>Orthodox</option>

                        </Form.Control>
                        </Col>
                    </Form.Group>

                    <Form.Group as={Row} controlId="formHorizontalDob">
                        <Form.Label column sm={2}>
                        Select City (For Trial Round)
                        </Form.Label>
                        <Col sm={10}>
                        <Form.Control sm={10} size="sm" as="select" name='city'  onChange={this.handleChange} >
                            <option>Select</option>
                            <option>Delhi</option>
                            <option>Noida</option>
                            <option>Gurugram</option>
                            <option>Mumbai</option>
                            <option>Pune</option>
                            <option>Nagpur</option>
                            <option>Agra</option>
                            <option>Meerut</option>
                            <option>Panipat</option>
                            <option>Lucknow</option>
                            <option>Kanpur</option>
                            <option>Jaipur</option>
                            <option>Udaipur</option>
                            <option>Ahemdabad</option>
                            <option>Surat</option>
                            <option>Vadodra</option>
                            <option>Chandigarh</option>
                            <option>Jalandhar</option>
                            <option>Dehradun</option>

                        </Form.Control>
                        </Col>
                    </Form.Group>

                    <Form.Group as={Row} controlId="formHorizontalDob">
                        <Form.Label column sm={2}>
                        Enter Refferal Code*
                        </Form.Label>
                        <Col sm={10}>
                        <Form.Control type="text" placeholder="Code" name='referral' onChange={this.handleChange} />
                        </Col>
                    </Form.Group>

                    <h3>Click For Payment*</h3>
                    

                    <Button type="submit" value="Submit">Sign in</Button>
            </Form>
        </div>
        )
    }
}


export default PadupForm;
